<template>
    <div id="table">
        <iheader :msg="title"></iheader>
        <div class="content">
            <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-change="importExcel"
                multiple
                :limit="3"
                accept=".xls,.xlsx"
            >
                <el-button size="small" type="primary"> 点击上传 </el-button>
            </el-upload>

            <el-table :data="tableData">
                <el-table-column prop="data1" label="数据1" width="">
                </el-table-column>
                <el-table-column prop="data2" label="数据2" width="">
                </el-table-column>
                <el-table-column prop="data3" label="数据3" width="">
                </el-table-column>
                <el-table-column prop="data4" label="数据4" width="">
                </el-table-column>
                <el-table-column prop="data5" label="数据5" width="">
                </el-table-column>
                <el-table-column prop="data6" label="数据6" width="">
                </el-table-column>
                <el-table-column prop="data7" label="数据7"> </el-table-column>
            </el-table>

            <pie :echartdata="echartdata" ref="myechart"></pie>
        </div>
    </div>
</template>

<script>
// import XLSX from "xlsx";
import * as XLSX from "xlsx/xlsx.mjs";
import iheader from "@/components/header.vue";
import pie from "@/components/piechart.vue";

export default {
    props: {},
    components: {
        iheader,
        pie,
    },
    data() {
        return {
            title: "table",
            fileList: [],
            tableData: [],
            resdata: [],
            resarr: [0, 0, 0, 0, 0, 0, 0],
            echartdata: [],
        };
    },
    methods: {
        importExcel(file) {
            // let file = file.files[0] // 使用传统的input方法需要加上这一步
            const types = file.name.split(".")[1];
            const fileType = [
                "xlsx",
                "xlc",
                "xlm",
                "xls",
                "xlt",
                "xlw",
                "csv",
            ].some((item) => item === types);
            if (!fileType) {
                alert("格式错误！请重新选择");
                return;
            }
            this.file2Xce(file).then((tabJson) => {
                if (tabJson && tabJson.length > 0) {
                    this.xlsxJson = tabJson;
                    // xlsxJson就是解析出来的json数据,数据格式如下
                    // [
                    //   {
                    //     sheetName: sheet1
                    //     sheet: sheetData
                    //   }
                    // ]
                    this.resdata = this.xlsxJson[0].sheet;
                    this.tableData = this.resdata;
                }
            });
            // 转换为echart数据
            this.editData();
        },

        file2Xce(file) {
            // eslint-disable-next-line no-unused-vars
            return new Promise(function (resolve, reject) {
                const reader = new FileReader();
                reader.onload = function (e) {
                    const data = e.target.result;
                    this.wb = XLSX.read(data, {
                        type: "binary",
                    });
                    const result = [];
                    this.wb.SheetNames.forEach((sheetName) => {
                        result.push({
                            sheetName: sheetName,
                            sheet: XLSX.utils.sheet_to_json(
                                this.wb.Sheets[sheetName]
                            ),
                        });
                    });
                    resolve(result);
                };
                reader.readAsBinaryString(file.raw);
                // reader.readAsBinaryString(file) // 传统input方法
            });
        },

        // 计算获取表格数据
        editData() {
            for (let i = 0; i < this.resdata.length; i++) {
                this.resarr[0] += this.resdata[i].data1;
                this.resarr[1] += this.resdata[i].data2;
                this.resarr[2] += this.resdata[i].data3;
                this.resarr[3] += this.resdata[i].data4;
                this.resarr[4] += this.resdata[i].data5;
                this.resarr[5] += this.resdata[i].data6;
                this.resarr[6] += this.resdata[i].data7;
            }
            // 将表格数据转为echart数据
            for (let i = 0; i < this.resarr.length; i++) {
                if (this.resarr[i] !== 0) {
                    this.echartdata.push({
                        value: this.resarr[i],
                        name: `第${i + 1}个`,
                    });
                }
            }
            this.$refs.myechart.myEcharts();
        },
    },
    mounted() {},
};
</script>

<style lang="less" scoped>
#table {
    .content {
        width: 90%;
        margin: 50px auto 0;
        border: 1px solid #ccc;

        /deep/ .el-table {
            border: 1px solid #ccc;
            margin-top: 20px;
            margin-bottom: 30px;
            // width: 80%;
            width: 600px;
            .el-table__header {
                .el-table__cell {
                    padding: 5px 0;
                    text-align: center;
                    .cell {
                        color: #999;
                    }
                }
            }
            .el-table__row {
                border: 1px solid pink;
                .el-table__cell {
                    padding: 3px 0;
                    text-align: center;
                    .cell {
                        color: #000;
                    }
                }
            }
        }

        /deep/ .upload-demo {
            .el-upload-list {
                display: none;
            }
        }
    }
}
</style>
